import emptyView from '../../components/emptyview.vue';

export default {
	data() {
		return {
			list: [],
			pageNum: 1,
			pageSize: 8,
			total: 1,
			amount: 0,
			balanceType: ['接单分成', '代理商分成', '提现'],
			agentId:''
		}
	},
	components: {
		emptyView
	},
	onLoad(options) {
		this.agentId = options.agentId
		
	},
	onShow() {
		this.pageNum = 1;
		this.total = 1;
		this.list = [];
		this.getUserAmounts();
	},
	onReady() {

	},
	onReachBottom() {
		this.getUserAmounts();
	},

	methods: {
		getUserAmounts() {
			let that = this;
			if (that.total <= (that.pageNum - 1) * that.pageSize) {
				return;
			}
			const params = {};
			params.pageNum = that.pageNum;
			params.pageSize = that.pageSize;
			params.agentId = that.agentId;
			that.$util.request({
				url: "/didi-patient/agentbalanceinfo/queryPage",
				param: params,
				success: function(res) {
					that.list = that.list.concat(res.data.list);
					that.pageNum += 1;
					that.total = res.data.total;
					for(var i = 0;i<that.list.length;i++){
						that.list[i].amount = that.list[i].amount/100;
					}
				},
				error: function() {}
			})
		}
	}
}
